<template>
  <div class="auth-wrapper auth-v2">
    <b-row class="auth-inner m-0">
      <b-col
          xl="7"

          class="d-none d-xl-flex align-items-center p-5"
      >
        <div
            class="w-100 d-xl-flex align-items-center justify-content-center px-5"
        >
          <b-img
              fluid
              :src="imgUrl"
              alt="Login V2"
          />
        </div>
      </b-col>
      <b-col
          lg="12"
          xl="5"
          class="d-flex align-items-center auth-bg p-lg-5 max-w-set"
          style="padding: 0;"
      >
        <b-col
            sm="8"
            md="6"
            lg="12"
            class="px-xl-2 mx-auto"
        >
          <div
              style="
              display: flex;
              justify-content: space-between;
              align-items: center;
            "
          >
            <b-link class="logo mt-2 text-right justify-content-start">
              <span style="margin-right: -15px">
                <vuexy-logo/>
              </span>
              <span class="text-primary brand-name">   </span>
            </b-link>
          </div>
          <validation-observer ref="loginValidation">
            <b-card class="py-4 px-2 mt-3 login-card">
              <b-row>
                <b-col
                    cols="12"
                    class="row"
                >
                  <h5
                      style="margin-bottom: 20px !important;    font-size: 20px;"
                  >
                        
                  </h5>
                  <small v-if="mobileSection" class="text-muted">
                             .
                  </small>
                  <small v-if="otpSection" class="text-muted">
                               .
                  </small>
                  <small v-if="buildingSection" class="text-muted">
                    {{ buildingData.comment }}
                  </small>
                </b-col>
              </b-row>
              <b-form
                  v-if="otpSection || mobileSection"
                  class="auth-login-form mt-2"
                  @submit.prevent
              >
                <!-- mobile -->
                <b-form-group
                    v-if="mobileSection"
                    label="  "
                    label-for="login-mobile"
                >
                  <validation-provider
                      #default="{ errors }"
                      name="Mobile"
                      rules="required|mobile"
                      :custom-messages="{ required: '      ', mobile: '        ' }"
                  >
                    <b-form-input
                        id="login-mobile"
                        v-model="userMobile"
                        :state="errors.length > 0 ? false:null"
                        name="login-mobile"
                        placeholder="09xxxxxxxxx"
                        maxlength="11"
                        style="text-align: left; font-weight: 300; letter-spacing: 0.1em; font-size: 120%; direction: ltr; padding-top: 10px;"
                    />
                    <small class="text-danger" style="display: block; min-height: 1.4em;">{{ errors[0] }}</small>
                  </validation-provider>
                </b-form-group>
                <!-- otp -->
                <b-form-group
                    v-if="otpSection"
                    label=" "
                    label-for="login-otp"
                >
                  <validation-provider
                      #default="{ errors }"
                      name="Otp"
                      rules="required"
                      :custom-messages="{ required: '      ' }"
                  >
                    <b-form-input
                        id="login-otp"
                        v-model="userOtp"
                        :state="errors.length > 0 ? false:null"
                        name="login-otp"
                        style="text-align: center; font-weight: 300; letter-spacing: 0.1em; font-size: 120%; direction: ltr; padding-top: 10px;"
                    />
                    <small class="text-danger">{{ errors[0] }}</small>
                  </validation-provider>
                </b-form-group>
                <b-button
                  variant="primary"
                  block
                  v-if="mobileSection && !loading"
                  class="login-button"
                  type="submit"
                  @click="login"
                >
                  
                </b-button>
                <b-button v-else-if="mobileSection && loading"
                          variant="primary"
                          block disabled>
                  <span class="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span>
                  
                </b-button>
<!--                <b-button-->
<!--                    v-if="mobileSection"-->
<!--                    class="login-button"-->
<!--                    type="submit"-->
<!--                    variant="primary"-->
<!--                    block-->
<!--                    @click="login"-->
<!--                >-->
<!--                  -->
<!--                </b-button>-->
                <b-button
                    v-if="otpSection"
                    class="login-button"
                    type="submit"
                    variant="primary"
                    block
                    @click="verify"
                >
                   
                </b-button>
              </b-form>
              <div v-if="buildingSection" class="mt-3">
                <b-row v-for="(building, index) in buildingData.building_list" :key="index">
                  <b-button block @click="selectBuilding(building.building_id)" class="mb-1"
                            variant="outline-secondary"
                  >
                    <h5>{{ building.name }}</h5>

                    <small style="display: block;" class="mt-1" >
                      {{ building.address }}
                    </small>
                  </b-button>
                </b-row>
              </div>
              <b-button
                  v-if="buildingData && buildingData.new_building"
                  type="submit"
                  variant="primary"
                  block
                  @click="registerBuilding"
              >
                  
              </b-button>
            </b-card>
          </validation-observer>
        </b-col>
      </b-col>
    </b-row>
  </div>
</template>

<script>
import {ValidationProvider, ValidationObserver} from 'vee-validate'
import VuexyLogo from '@core/layouts/components/Logo.vue'
import {
  BRow,
  BCol,
  BLink,
  BFormGroup,
  BFormInput,
  BInputGroupAppend,
  BInputGroup,
  BFormCheckbox,
  BCard,
  BCardText,
  BCardTitle,
  BImg,
  BForm,
  BButton,
  BFormRadio,
} from 'bootstrap-vue'
import {required, mobile} from '@validations'
import {togglePasswordVisibility} from '@core/mixins/ui/forms'
import store from '@/store/index'
import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'
import axios from "@/libs/axios";
import authService from "@/api/authService";

export default {
  name: "login",
  components: {
    BRow,
    BCol,
    BLink,
    BFormGroup,
    BFormInput,
    BInputGroupAppend,
    BInputGroup,
    BFormCheckbox,
    BCard,
    BCardText,
    BCardTitle,
    BImg,
    BForm,
    BButton,
    BFormRadio,
    VuexyLogo,
    ValidationProvider,
    ValidationObserver,
  },
  mixins: [togglePasswordVisibility],
  data() {
    return {
        loading: false,
      mobileSection: true,
      otpSection: false,
      buildingSection: false,
      userMobile: '',
      userOtp: '',
      buildingData: null,
      sideImg: require('@/assets/images/pages/login-v2.svg'),
      // validation rulesimport store from '@/store/index'
      required,
      mobile,
    }
  },
  computed: {
    imgUrl() {
      if (store.state.appConfig.layout.skin === 'dark') {
        // eslint-disable-next-line vue/no-side-effects-in-computed-properties
        this.sideImg = require('@/assets/images/pages/login-v2-dark.svg')
        return this.sideImg
      }
      return this.sideImg
    },
  },
  methods: {
    login() {
        this.loading = true
      authService.login({
        mobile: this.userMobile
      })
          .then(response => {
              this.loading = false
            this.$refs.loginValidation.validate()
                .then(success => {
                  if (success) {
                    this.$toast({
                      component: ToastificationContent,
                      props: {
                        title: '  ',
                        icon: 'EditIcon',
                        variant: 'success',
                      },
                    })
                    this.mobileSection = false
                    this.otpSection = true
                  }
                })
          })
          .catch(error => {
              this.loading = false
            // this.$toast({
            //   component: ToastificationContent,
            //   props: {
            //     title: error.response.data.message,
            //     icon: 'EditIcon',
            //     variant: 'error',
            //   },
            // })
          })
    },
    async verify() {
      authService.verify({
        mobile: this.userMobile,
        code: this.userOtp,
        manager: 1,
        building_id: null
      })
          .then(response => {
            if (response.data.building_list && response.data.building_list.length) {
              this.otpSection = false
              this.buildingSection = true
              this.buildingData = response.data
            } else {
              localStorage.setItem('token', response.data.token)
              switch (response.data.redirect) {
                case 'register-dashboard' :
                  setTimeout(() => {document.location.href = '/owner-resident'}, 50)
                  break

                case 'home' :
                  setTimeout(() => {document.location.href = '/'}, 50)
                  break

                default:
                  this.$router.push({name: 'register'})
                  break
              }
            }
          })
          .catch(error => {
            // this.$toast({
            //   component: ToastificationContent,
            //   props: {
            //     title: error.response.data.message,
            //     icon: 'EditIcon',
            //     variant: 'error',
            //   },
            // })
          })
    },
    selectBuilding(buildingId) {
      authService.selectBuilding({
        mobile: this.buildingData.mobile,
        code: this.buildingData.code,
        manager: 1,
        building_id: buildingId,
      })
          .then(response => {
            localStorage.setItem('token', response.data.token)
              switch (response.data.redirect) {
                case 'register-dashboard' :
                  setTimeout(() => {document.location.href = '/owner-resident'}, 50)
                  break

                case 'home' :
                  setTimeout(() => {document.location.href = '/'}, 50)
                  break

                default:
                  this.$router.push({name: 'register'})
                  break
              }
          })
          .catch(error => {
            // this.$toast({
            //   component: ToastificationContent,
            //   props: {
            //     title: error.response.data.message,
            //     icon: 'EditIcon',
            //     variant: 'error',
            //   },
            // })
          })
    },
  },

}
</script>

